/**
 * Различные константы про платформу.
 * Лучше не добавлять зависимостей в этот файл, т.к. он используется логгером
 * и получаются циклические зависимости.
 */

'use strict';

const visbookmarksFakeTabUrl = '';
const visbookmarksUrl = 'chrome://newtab/';
const visbookmarksRealUrl = chrome.runtime.getURL('/layout/newtab.html');

module.exports = {
    /**
     * для опознания платформы
     */
    id: 'chr',

    /**
     * также для опознания платформы, но удобнее
     */
    isChr: true,

    /**
     * Id расширения
     */
    extensionId: chrome.runtime.id,

    /**
     * Client ID платформы
     */
    cid: 72482,

    /**
     * vbID в локализации, используется во ссылках
     */
    type: 'vb-chrome',

    /**
     * параметр, записываемый в ys куку вместе с версией и отправляемый в soft-export статистику
     */
    yasoft: 'vbch',

    /**
     * Имя браузера, используется в dayuse-exp статистике
     */
    browserName: 'chrome',

    /**
     * Ссылка на таб с ВЗ
     */
    visbookmarksUrl,

    /**
     * Ссылка на фейковый таб с ВЗ (about:tabs в ИЕ, yafd:tabs в Firefox)
     */
    visbookmarksFakeTabUrl,

    /**
     * Полная ссылка на html страницу с ВЗ
     */
    visbookmarksRealUrl,

    /**
     * Паттерны урлов которые исключаем из списка закрытых табов
     */
    vbUrls: [
        visbookmarksUrl,
        visbookmarksFakeTabUrl,
        visbookmarksRealUrl,
    ],
};



