/**
 * Вывод сообщений в табы ВЗ и файл лога
 *
 * logger.error('error caught')
 * logger.info('Smth intresting happened: %s', exception)
 * logger.warn('Current time: %i', Date.now())
 *
 * Возможные плейсхолдеры:
 * %s - string
 * %d, %i - integer
 * %f - float
 * %o, %j - json
 * %l - limited string (300 characters)
 */
'use strict';

var lggr = require('lggr');
const buildInfo = require('../platform/build-info');
var logConfig = require('../platform/log-config');
var logUtils = require('./log-utils');

const errorBooster = require('./error-booster');

var methods = ['info', 'warn', 'error'];

var options = {
    methods: methods,
    writers: logConfig.writers,
    formatters: logConfig.formatters,
    levels: {}
};

var logger = new lggr.Logger('core', options);
const cloneLogger = logger.clone.bind(logger);

logger.create = (prefix) => {
    const loggerClone = cloneLogger(prefix);

    loggerClone.error = (message, ...args) => {
        const [formattedMessage] = logUtils.fileFormatter(
            'error', loggerClone._prefix, [message, ...args]);
        if (buildInfo.isDebug()) {
            console.error(formattedMessage);
        } else {
            errorBooster.error(formattedMessage);
        }
    };

    loggerClone.info = (message, ...args) => {
        const [formattedMessage] = logUtils.fileFormatter(
            'info', loggerClone._prefix, [message, ...args]);
        if (buildInfo.isDebug()) {
            console.log(formattedMessage);
        } else {
            // Do not send info.
            // errorBooster.info(formattedMessage);
        }
    };

    loggerClone.warn = (message, ...args) => {
        const [formattedMessage] = logUtils.fileFormatter(
            'warn', loggerClone._prefix, [message, ...args]);
        if (buildInfo.isDebug()) {
            console.warn(formattedMessage);
        } else {
            errorBooster.warn(formattedMessage);
        }
    };

    return loggerClone;
};

module.exports = logger;



