/**
 * Helpers for logger: common formatters and utility methods
 */
'use strict';

var lggr = require('lggr');

function toUpperCase(str) {
    return str ? str.toUpperCase() : '';
}

function toLowerCase(str) {
    return str ? str.toLowerCase() : '';
}

// replaces unsupported placeholders for native console methods
var normalizePlaceholders = lggr.createNormalFormatter({
    j: 'o',
    l: 's'
});

var mutatePrefix = function (prefix) {
    return '[' + prefix.toLowerCase() + ']';
};

module.exports = {
    toUpperCase: toUpperCase,
    toLowerCase: toLowerCase,

    normalizePlaceholders: normalizePlaceholders,

    consoleFormatter: lggr.combineFormatters([
        // replaces unsupported placeholders for native console methods
        normalizePlaceholders,
        // adds prefix to log arguments
        lggr.createPrefixFormatter(mutatePrefix),
        // joins first two arguments (prefix and string with placeholders)
        lggr.createJoinFirstFormatter(2)
    ]),

    fileFormatter: lggr.combineFormatters([
        // replaces placeholders
        lggr.createPlaceholdersFormatter(),
        // adds prefix to log arguments
        lggr.createPrefixFormatter(mutatePrefix),
        // adds method to log arguments
        lggr.createMethodFormatter(toUpperCase),
        // adds date to log arguments
        lggr.createDateFormatter(),
        // joins all log arguments
        lggr.createJoinFormatter()
    ])
};



