/**
 * Модуль alarms.
 *
 * Регистрирует алармы (таймеры), с учётом уже созданных.
 *
 * См. также https://developer.chrome.com/apps/alarms
 */

'use strict';

var formatDate = require('./date').format;
var logger = require('./logger').create('Alarms');
var ChannelCacher = require('./channel-cacher');
var noop = require('./noop');

var cacher = new ChannelCacher();
var alarmsHandlers = new Map();

module.exports = {
    /**
     * Начинаем кешировать вызовы канала
     */
    startCaching: function () {
        cacher.attach(chrome.alarms.onAlarm);
        cacher.channel.addListener(function (alarm) {
            logger.info('Alarm triggered: %s (cached: %s)', alarm.name, cacher.isCaching());
        });
    },
    /**
     * Вызывает сработавшие обработчики и прекращает кеширование
     */
    stopCaching: function () {
        cacher.stopCaching();
    },
    /**
     * Регистрация таймера.
     *
     * @param  {Object} alarmInfo
     * @param  {String} alarmInfo.name Имя, идентификатор таймера
     * @param  {Number} [alarmInfo.when] Время срабатывания таймера, в миллисекундах
     * @param  {Number} [alarmInfo.delayInMinutes] Время до срабатывания таймера, в минутах
     * @param  {Number} [alarmInfo.periodInMinutes] Интервал, в минутах
     * @param  {Function} [handler] обработчик
     */
    register: function (alarmInfo, handler) {
        if (!alarmInfo.name) {
            throw new Error('You should provide alarm name');
        }
        // Создаём таймер только если такого таймера ещё нет или если значение периода срабатывания
        // создаваемого таймера отлично от значения этого периода у существующего таймера.
        this.existsEqual(alarmInfo, function (exists) {
            if (exists) {
                return;
            }
            logger.info('Creating alarm %s with options %j', alarmInfo.name, alarmInfo);
            this._registerAlarm(alarmInfo);
        }.bind(this));
        this._registerHandler(alarmInfo.name, handler);
    },

    /**
     * Подписка на канал кешэра
     * @param {String} alarmName
     * @param {Function} handler
     */
    on: function (alarmName, handler) {
        var handlers = alarmsHandlers.get(alarmName) || [];
        var alarmHandler = function (alarm) {
            if (alarm.name === alarmName) {
                handler();
            }
        };
        handlers.push(alarmHandler);
        alarmsHandlers.set(alarmName, handlers);
        cacher.channel.addListener(alarmHandler);
    },

    /**
     * Отписка от канала кешэра
     * @param {String} alarmName
     */
    off: function (alarmName) {
        var handlers = alarmsHandlers.get(alarmName);
        if (handlers) {
            handlers.forEach(function (handler) {
                cacher.channel.removeListener(handler);
            });
            alarmsHandlers.delete(alarmName);
        }
    },

    /**
     * Существует ли таймер
     * @param {String} alarmName
     * @param {Function} callback
     */
    exists: function (alarmName, callback) {
        chrome.alarms.get(alarmName, function (alarm) {
            callback(Boolean(alarm));
        });
    },

    /**
     * Существует ли идентичный (с аналогичным периодом) аларм
     * @param {Object} alarmInfo
     * @param {Function} callback
     */
    existsEqual: function (alarmInfo, callback) {
        chrome.alarms.get(alarmInfo.name, function (alarm) {
            var period = alarmInfo.periodInMinutes || null;
            var hasSameAlarm = alarm && alarm.periodInMinutes === period;
            callback(hasSameAlarm);
        });
    },

    /**
     * Удалить таймер
     * @param {String} alarmName
     * @param {Function} [callback]
     */
    clear: function (alarmName, callback) {
        callback = callback || noop;
        this.off(alarmName);
        chrome.alarms.clear(alarmName, callback);
    },

    /**
     * Триггерим таймер вручную
     * @param {String} alarmName
     */
    trigger: function (alarmName) {
        cacher.channel.dispatch({name: alarmName});
    },

    /**
     * Выводит список существующих таймеров
     */
    logAll: function () {
        chrome.alarms.getAll(function (alarms) {
            if (!Array.isArray(alarms) || !alarms.length) {
                logger.info('No alarms found');
            } else {
                logger.info('Existing alarms count: %i', alarms.length);
                alarms.forEach(function (alarm) {
                    var scheduledTime = alarm.scheduledTime
                        ? formatDate(alarm.scheduledTime, '%Y-%M-%D %H:%N')
                        : '';
                    var periodInHours = alarm.periodInMinutes ? alarm.periodInMinutes / 60 : '';
                    logger.info('Existing alarm: %s scheduled time %s (period: %f hours)',
                        alarm.name, scheduledTime, periodInHours);
                });
            }
        });
    },

    /**
     * Сбрасываем все алармы и очищаем кешэр
     */
    flush: function () {
        chrome.alarms.clearAll();
        alarmsHandlers.clear();
        cacher = new ChannelCacher();
    },

    /**
     * @param {Object} alarmInfo
     * @private
     */
    _registerAlarm: function (alarmInfo) {
        var props = Object.assign({}, alarmInfo);
        delete props.name;
        chrome.alarms.create(alarmInfo.name, props);
    },

    /**
     * @param {String} alarmName
     * @param {Function} [handler]
     * @private
     */
    _registerHandler: function (alarmName, handler) {
        if (typeof handler === 'function') {
            this.on(alarmName, handler);
        }
    }
};



