/**
 * Инфа про браузер
 */

'use strict';

var about = require('../platform/about');

var cache = {};

module.exports = {
    CHROME_ID: 'chr',
    FX_ID: 'fx',
    IE_ID: 'ie',

    get name() {
        cache.nameVersion = cache.nameVersion || extractNameVersion();
        return cache.nameVersion[0];
    },

    get version() {
        cache.nameVersion = cache.nameVersion || extractNameVersion();
        return cache.nameVersion[1];
    },

    get os() {
        if (!cache.os) {
            var platformName = navigator.platform.toLowerCase();
            if (platformName.indexOf('win') === 0) {
                cache.os = 'win';
            } else if (platformName.indexOf('mac') === 0) {
                cache.os = 'mac';
            } else {
                cache.os = navigator.platform || 'unknown';
            }
        }
        return cache.os;
    },

    get platformId() {
        return about.id;
    },

    /**
     * @param {Int?} majorVersion для проверки на конкретную версию
     * @returns {Boolean}
     */
    isChrome: function (majorVersion) {
        return about.id === module.exports.CHROME_ID &&
            (majorVersion ? isMajorVersion(majorVersion) : true);
    },

    /**
     * @param {Int?} majorVersion для проверки на конкретную версию
     * @returns {Boolean}
     */
    isFirefox: function (majorVersion) {
        return about.id === module.exports.FX_ID &&
            (majorVersion ? isMajorVersion(majorVersion) : true);
    },

    /**
     * @param {Int?} majorVersion для проверки на конкретную версию
     * @returns {Boolean}
     */
    isIE: function (majorVersion) {
        return about.id === module.exports.IE_ID &&
            (majorVersion ? isMajorVersion(majorVersion) : true);
    },
};

// https://stackoverflow.com/questions/2400935/browser-detection-in-javascript
function extractNameVersion() {
    var ua = navigator.userAgent;
    var tem;
    var M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
    if (/trident/i.test(M[1])) {
        tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
        return ['msie', (tem[1] || '')];
    }
    M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
    if ((tem = ua.match(/version\/(\d+)/i)) !== null) {
        M.splice(1, 1, tem[1]);
    }
    return M;
}

/**
 * Определяем мажорную версию
 */
function isMajorVersion(majorVersion) {
    var version = module.exports.version;
    majorVersion = String(majorVersion);
    return !version || version === majorVersion || version.indexOf(majorVersion + '.') === 0;
}



